.d-flex {
  display: flex; }

.a-center {
  align-items: center; }

.j-beetween {
  justify-content: space-between; }

/* page */
.header-page {
  margin-bottom: 2rem; }

.header-page__inner {
  background: #0f2e53;
  padding: 15px 0;
  margin-bottom: 24px; }

.header-page__item {
  display: flex;
  align-items: center; }
  .header-page__item:first-child {
    margin-right: 5rem; }
  .header-page__item svg {
    margin-right: 1rem; }
  .header-page__item .header-page__phone-number {
    display: block;
    font-family: "gilroybold";
    font-size: 18px;
    line-height: 22px; }
  .header-page__item .header-page__pochta {
    font-size: 16px;
    line-height: 19px; }
  .header-page__item span {
    font-size: 16px;
    line-height: 19px;
    color: #fff; }

.header-page__search {
  width: 100%; }
  .header-page__search .header-page__input {
    width: inherit;
    background: #ffffff;
    border: 1px solid #c4c4c4;
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.05);
    border-radius: 10px;
    padding: 13px 20px;
    height: 5rem;
    position: relative;
    background-image: url(../../images/icons/search.svg);
    background-position: center right 20px;
    background-repeat: no-repeat; }
    .header-page__search .header-page__input::placeholder {
      color: #c4c4c4;
      font-size: 14px;
      line-height: 17px; }

.navbar-list {
  display: flex;
  align-items: center;
  justify-content: space-between; }
  .navbar-list .navbar-list__item:not(:last-child) {
    margin-right: 35px; }
  .navbar-list .navbar-list__item a {
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    color: #000;
    white-space: nowrap;
    transition: all 0.3s ease; }
    .navbar-list .navbar-list__item a:hover {
      color: #206BB0; }

.footer-page {
  background: #0F2E53; }

.footer-page__top {
  padding-top: 140px;
  padding-bottom: 50px; }

.footer-page__bottom {
  background: #153963;
  padding: 16px 0; }
  .footer-page__bottom span {
    font-size: 16px;
    line-height: 19px;
    color: #648DBD;
    display: block;
    margin: auto;
    text-align: center; }

.footer-page__info .info-title {
  color: #E2CE19;
  font-size: 16px;
  line-height: 19px;
  margin-bottom: 25px; }

.info-list__item {
  display: flex;
  align-items: center; }
  .info-list__item:not(:last-child) {
    margin-bottom: 3rem; }
  .info-list__item svg {
    flex-shrink: 0;
    margin-right: 1rem; }
  .info-list__item a {
    font-size: 16px;
    line-height: 19px; }
  .info-list__item .text-bold {
    font-family: "gilroybold"; }

.footer-page__content .navbar-list__item:not(:last-child) {
  margin-right: 30px; }

.footer-page__content .navbar-list__item a {
  color: #FFFFFF;
  letter-spacing: -0.9px; }

.footer-page__follow {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 47px; }

.footer-page__follow-item .title {
  text-transform: uppercase;
  font-size: 24px;
  line-height: 30px;
  font-weight: 600;
  color: #FFFFFF;
  margin-bottom: 1rem; }
  .footer-page__follow-item .title span {
    color: #E2CE19; }

.footer-page__follow-item .description {
  font-size: 16px;
  line-height: 19px;
  color: #FFFFFF; }

.footer-page__follow-item .follow-input {
  display: flex;
  align-items: center;
  margin-top: 3rem;
  min-height: 5rem;
  width: 100%; }
  .footer-page__follow-item .follow-input input {
    background: #FFFFFF;
    border-radius: 10px 0 0 10px;
    padding: 16px;
    width: 100%;
    min-height: inherit; }
  .footer-page__follow-item .follow-input button {
    background: #206BB0;
    border-radius: 0px 10px 10px 0px;
    min-height: inherit;
    font-size: 16px;
    line-height: 19px;
    text-transform: uppercase;
    padding: 0 5rem;
    color: #FFFFFF; }

.social-list .social-list__item:not(:last-child) {
  margin-bottom: 2rem; }

.social-list .social-list__item .social-list__icon {
  width: 32px;
  height: 32px;
  display: block;
  flex-shrink: 0; }
  .social-list .social-list__item .social-list__icon img {
    max-width: 100%;
    max-height: 100%; }

.section-page.blue-page {
  padding: 62px 0;
  background: url(../images/img/blue-page.jpg), linear-gradient(264.49deg, #235481 0%, #0F2E53 99.52%);
  border-radius: 0px;
  width: 100%;
  height: 60rem;
  background-position: center center;
  background-size: cover;
  color: #fff; }

.blue-page__heading {
  margin: 0 auto;
  text-align: center;
  margin-bottom: 65px; }
  .blue-page__heading .title {
    font-weight: 600;
    margin-bottom: 1rem;
    position: relative;
    max-width: 371px;
    margin: 0 auto; }
    .blue-page__heading .title::before {
      content: "";
      position: absolute;
      width: 90px;
      background-color: #FFFFFF;
      height: 1px;
      left: -50%;
      top: 50%; }
    .blue-page__heading .title::after {
      content: "";
      position: absolute;
      width: 90px;
      background-color: #FFFFFF;
      height: 1px;
      right: -50%;
      top: 50%; }

.plus-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center; }
  .plus-item:first-child {
    margin-bottom: 5rem; }
  .plus-item .plus-icon {
    background: #FFFFFF;
    width: 8rem;
    height: 8rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    margin-bottom: 2rem; }
  .plus-item .text {
    font-size: 18px;
    line-height: 21px; }

.section-page.tech-list {
  margin-bottom: 23px; }

.section-page.garant-info {
  padding: 5rem 0; }

.tech-list__item {
  display: flex;
  flex-direction: column;
  align-items: center; }
  .tech-list__item img {
    flex-shrink: 0;
    margin-bottom: 1rem; }
  .tech-list__item span {
    text-align: center;
    display: block; }

.splide__slide {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 70rem; }

.splide__slide:nth-child(1) {
  background-image: url(../../images/img/hero-1.jpg); }

.splide__slide:nth-child(2) {
  background-image: url(../../images/img/hero-2.jpg); }

.splide__slide:nth-child(3) {
  background-image: url(../../images/img/hero-4.jpg); }

.splide__slide .container {
  height: 100%; }

.splide__info {
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  justify-content: center;
  height: inherit;
  margin-left: 72px;
  color: #fff; }
  .splide__info .title {
    font-size: 36px;
    line-height: 42px;
    max-width: 460px;
    font-weight: 600;
    margin-bottom: 4rem;
    position: relative; }
    .splide__info .title::after {
      content: "";
      position: absolute;
      width: 50%;
      height: 1px;
      background-color: #E2CE19;
      left: 0;
      bottom: -20px; }
  .splide__info .description {
    font-size: 16px;
    line-height: 19px;
    margin-bottom: 5rem; }
  .splide__info .btn {
    background: #206BB0;
    border-radius: 10px;
    height: 5rem;
    text-transform: uppercase;
    min-width: 209px;
    display: flex;
    align-items: center;
    justify-content: center; }

.splide__arrows .splide__arrow {
  background-color: transparent; }
  .splide__arrows .splide__arrow svg {
    fill: #E2CE19;
    width: 2rem;
    height: 2rem; }

.splide__arrows .splide__arrow--prev {
  left: 17.2%; }

.splide__arrows .splide__arrow--next {
  right: 17.2%; }

.garant-info__content .title {
  font-size: 36px;
  line-height: 42px;
  color: #000;
  font-weight: 600;
  max-width: 685px;
  margin-bottom: 67px;
  position: relative; }
  .garant-info__content .title::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -17px;
    width: 50%;
    height: 1px;
    background-color: #E2CE19; }

.content-list .content-list__item {
  display: flex;
  align-items: center; }
  .content-list .content-list__item:not(:last-child) {
    margin-bottom: 3rem; }
  .content-list .content-list__item img {
    flex-shrink: 0;
    margin-right: 4rem; }
  .content-list .content-list__item span {
    line-height: 26px;
    color: #000; }

/* default */
